	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapter 18 – Revélation","texte":{"childMarkdownRemark":{"html":"<p>Darakei was throwing something at the ground, still angry about his test subjects escaping. He still had a few test subjects left.</p>\n<p>Éléa: Honey, calm down, you'll end up hurting yourself...<br>\nDarakei: I wanted to train them when they have good control over their powers. We need to find them...<br>\nÉléa: Maybe they didn't want to stay there...<br>\nDarakei: Maybe, but I don't understand how they stay untraceable...</p>\n<p>Éléa shrugs her shoulders and begins to pick up the shards of glass.</p>\n<p>Darakei: Sorry, darling.</p>\n<p>Darakei comes over and kisses her on the cheek and picks up the stuff for her.</p>\n<p>Éléa: You know what I think?<br>\nDarakei: Go.<br>\nÉléa: They will act the right way, as I understand it, Wamien is the leader of the group so I have good hope.<br>\nDarakei: How do you know that?<br>\nÉléa: Well, I read the reports...</p>\n<p>Éléa looks away trying to hide something.</p>\n<p>Darakei: The reports are in the lab, I didn't bring them home.<br>\nÉléa: I sneak in when I get bored at home. Being a lawyer sucks, you know?<br>\nDarakei: And how long have you been doing this?<br>\nÉléa: A few months.<br>\nDarakei: You're sneaking around like invisible?</p>\n<p>Éléa nodded without adding anything.</p>\n<p>Darakei Good, okay... I'll work in my office.<br>\nÉléa: If you need anything, just let me know, okay?<br>\nDarakei: No problem.</p>\n<p>Darakei went to his office, he thought he should look at the surveillance cameras near Wamien's cell. He went back the day he ran away from the lab and activated the infrared. He made the big eyes when he saw his wife's figure.</p>\n<p>Darakei: Well, damn it... Éléa!!!<br>\nÉléa: Yeah?</p>\n<p>Darakei turns his frozen screen towards her.</p>\n<p>Darakei: Can you explain?<br>\nÉléa: Heuuuu<br>\nDarakei: An onomatopoeia is not a valid explanation.<br>\nÉléa: I have my reasons.<br>\nDarakei: Well, expose them to me because I'm gonna yell.<br>\nÉléa: Because he didn't deserve to be there like the others he came after.<br>\nDarakei: So you were there when he came back and hurt three quarters of my people?<br>\nÉléa: Yes...<br>\nDarakei: I'm being betrayed by my wife.<br>\nÉléa: I know where they are, but I wouldn't say so because they have the right to peace and to live their lives without being locked in cells.<br>\nDarakei: You know where they are too! What's your little lover you meet at midnight?<br>\nÉléa: Not at all, you are my husband and I love you. I knew you wouldn't agree to get them out of there, so I sneaked in.<br>\nDarakei: Okay... are they even okay?<br>\nÉléa: Yes, they are fine, they are with another woman who possesses powers in a natural way.<br>\nDarakei: Hm, I thought we're not the only ones with powers.<br>\nÉléa: You think what, there are more than a few hundred on the planet and even more who have non-active genes.<br>\nDarakei: You did some research in your own corner.<br>\nÉléa: Yes, unfortunately some meet and have bad intentions.<br>\nDarakei: Like the Black Eagles of the neighbouring kingdom for example.<br>\nÉléa: Exactly, they will take care of the band soon after what Wamien told me a few days ago.<br>\nDarakei: Seriously, will they take care of his criminals, like kill them or put them in jail?<br>\nÉléa: I don't know about that.</p>"}},"chapitreAvant":"chapter-17-the-report","chapitreApres":"chapter-19-perfecting-skills","nomRoman":"the-first-cyborg-volume-1","slug":"chapter-18-revelation","codeChapitre":"H0001C019","equivalentUrl":"/histoires/chapitre/chapitre-18-la-revelation"}},"pathContext":{"slug":"chapter-18-revelation"}}

